import React from "react";
import logoImage from "./Resources/raybanlogo.png";
import './Navbar.css'

export default function Navbar() {
  return (
    <>
      <div id="nav">
        <div id="nLeft">
          <img id="logoImage" src={logoImage} alt="logo"></img>
          <a href="/">Products</a>
          <a href="/">About</a>
        </div>
        <div id="nRight">
          <a href="/">Luxe</a>
          <a href="/">Pricing</a>
        </div>
      </div>
    </>
  );
}
