import React from "react";
import Navbar from "./Navbar";
import imageOne from "./Resources/IMG_7564.JPG";
import imageTwo from "./Resources/IMG_7565.JPG";
import imageThree from "./Resources/IMG_7566.JPG";
import imageFour from "./Resources/IMG_7567.JPG";
import imageFive from "./Resources/IMG_7568.JPG";
import Shery from "sheryjs";
// import ControlKit from "controlkit";
import "./Main.css";
import Hero from "./Hero";

export default function Main() {
  Shery.imageEffect("#back", {
    style: 5,
    config: {
      a: { value: 1.49, range: [0, 30] },
      b: { value: -0.98, range: [-1, 1] },
      aspect: { value: 1.28822947576656774 },
      gooey: { value: true },
      infiniteGooey: { value: true },
      durationOut: { value: 1, range: [0.1, 5] },
      durationIn: { value: 1, range: [0.1, 5] },
      displaceAmount: { value: 0.5 },
      masker: { value: true },
      maskVal: { value: 1.33, range: [1, 5] },
      scrollType: { value: 0 },
      geoVertex: { range: [1, 64], value: 1 },
      noEffectGooey: { value: false },
      onMouse: { value: 0 },
      noise_speed: { value: 1.59, range: [0, 10] },
      metaball: { value: 0.21, range: [0, 2] },
      discard_threshold: { value: 0.5, range: [0, 1] },
      antialias_threshold: { value: 0, range: [0, 0.1] },
      noise_height: { value: 0.47, range: [0, 2] },
      noise_scale: { value: 12.15, range: [0, 100] },
    },
    gooey: true, debug: true,
  });
  return (
    <>
      <div id="main">
        <div id="back">
          <img src={imageOne} alt="imageOne"></img>
          <img src={imageTwo} alt="imageTwo"></img>
          <img src={imageThree} alt="imageThree"></img>
          <img src={imageFour} alt="imageFour"></img>
          <img src={imageFive} alt="imageFive"></img>
        </div>
        <div id="top">
          <div id="workingArea">
            <Navbar />
            <Hero />
          </div>
        </div>
      </div>
    </>
  );
}
